@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;600;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
}

body {
    background-color:aliceblue ;
    color: #222;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
}

.wrapper {
    max-width: 1280px;
    margin: 50px auto;
}

header .logo {
    font-weight: 600;
    font-size: 20px;
}

header ul.nav {
    float: right;
    list-style: none;
}

header ul.nav li {
    display: inline;
    margin-left: 25px;
    cursor: pointer;
    transition: opacity 500ms ease;
}

header ul.nav li:hover {
    opacity: 0.5;
}


header .presentation {
    position: relative;
    margin: 50px 0;
    background: url(./img/backgroundimg.jpg) no-repeat;
    width: 100%;
    height: 500px;
    background-size: cover;
    background-position: center center;
    background-blend-mode: multiply;
    background-color: gray;
}

header .presentation::after{
    position: absolute;
    top: 50px;
    left: 50px;
    width: 1280px;
    font-size: 50px;
    font-weight: 800;
    color: white;
    content: "Лучшие товары для вашего дома";
}

header .presentation::before{
    position: absolute;
    top: 150px;
    left: 50px;
    width: 1280px;
    font-size: 24px;
    font-weight: 300;
    color: white;
    content: "По самым низким ценам";
}

footer {
    text-align: center;
    margin-top: 100px;
}